.addFormGlobal{
    background: #fff;
    padding: 60px;
    h2{
        text-align: center;
        margin-bottom: 30px;
        color: #333;
    }
    .row{
        --cui-gutter-y: 20px;
    }
}
.arTableWrapper{
    background: #fff;
}
.clinic_details{
    padding: 30px;
    background-color: #fff;
}
.search_doctor_wrp {
    position: relative;
    ul{
        list-style: none;
        padding-left: 0;
        position: absolute;
        top: 100%;
        left: 0;
        margin: 0;
        width: 100%;
        background: #fff;
        box-shadow: 1px 7px 10px rgba(0, 0, 0, 0.1);
        li{
            padding: 10px 20px;
            display: block;
            cursor: pointer;
        }
    }
}